

















:root {
  --accent-color: #9bb537;
  --primary-text-color: #050505;
  --secondary-text-color: #fff;
  --primary-text-color-08: rgba(5, 5, 5, .8);
  --primary-text-color-05: rgba(5, 5, 5, .5);
  --primary-text-color-03: rgba(5, 5, 5, .3);
  --primary-text-color-02: rgba(5, 5, 5, .2);
  --dark-theme-text-color-08: rgba(255, 255, 255, .8);
  --dark-theme-text-color-05: rgba(255, 255, 255, .5);
  --dark-theme-text-color-03: rgba(255, 255, 255, .3);
  --light-text-color: #f8f8f8;
  --switch-bg-off-color: #cecdcd;
  --light-background-color: #f8f8f8;
  --dark-background-color: #161616;
  --animation-set: 400ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--light-background-color);
  color: var(--primary-main-color);
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
}

img {
  display: block;
}

.container {
  max-width: 1280px;
  outline: 1px solid green;
  margin: auto;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (max-width: 374px) {
  .container {
    max-width: 374px;
    padding: 0 8px;
  }
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .container.categories, .container.popular {
    width: 176px;
    padding: 0;
  }

  .container.filter, .container.cards {
    width: 496px;
    padding: 0;
  }

  .tablet-desktop {
    width: 768px;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 32px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .container, .tablet-desktop {
    width: 1280px;
    padding: 0 100px;
  }

  .container.categories, .container.popular {
    width: 235px;
    padding: 0;
  }

  .container.filter, .container.cards {
    width: 782px;
    padding: 0;
  }
}

